import './Picture.css';
import { Carousel } from 'react-bootstrap';

export function Picture() {
    let arr: number[] = new Array(14).fill(0);
    const onErrorImg = (e:any) => {
        e.target.src = `./assets/img/no-image.png`;
    }

    const imgRender = () => {
        let result: any[] = [];
        for (let i = 0; i < arr.length; i++) {
            // let index : number = i+1;
            result.push(
                <Carousel.Item>
                    <div className='pictrue-contianer'>
                    <img
                        className="picture-img d-block w-100"
                        src={`./assets/img/${(i+1)}.JPG`}
                        onError={onErrorImg}
                    />
                    </div>

                    <Carousel.Caption>
                        <h3>연애시절</h3>
                        <p>아직 스튜디오사진이 없어 연애시절 사진으로 대체합니다.</p>
                    </Carousel.Caption>
                </Carousel.Item>
            );
        }
        return result;
    }

    return (
        <>
            <Carousel fade>
                {imgRender()}
            </Carousel>
        </>

    )
}



